@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 24px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 60px;
  font-weight: 300;
  line-height: 1;
}

h2 {
  font-size: 48px;
  font-weight: 300;
  line-height: 1;
}

a {
  text-decoration: none;
}

a:hover {
  color: #C00000;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}

.content-wrapper {
  margin: 64px 415px;
}

.primary-image {
  border-bottom: 1px solid #cccccc;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

@media only screen and (max-width: 1080px) {
  .content-wrapper {
    margin: 64px 192px;
  }
}

@media only screen and (max-width: 768px) {
  .content-wrapper {
    margin: 64px 50px;
  }
}
.ngc-button {
  border: 0;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
}

.ngc-button--primary {
  color: white;
  width: auto;
  height: 220px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding-right: 24px;
}

.ngc-button--secondary {
  color: black;
  font-size: 20px;
  background-color: transparent;
}

.ngc-button--primary svg {
  margin-top: 47px;
}

.ngc-button--primary p {
  line-height: 1.4;
  margin-top: 41px;
  margin-left: 32px;
  font-size: 30px;
}

.ngc-button--secondary-dark {
  color: white;
}

.ngc-button--secondary.selected {
  color: #C00000;
}

.ngc-button--secondary-dark.selected {
  color: #ff2929;
}
.ngc-logo-bordered {
    border: 1px solid white;
    padding: 1.4em;
}
.wrapper {
  border-bottom: 1px solid #CCCCCC;
  padding: 36px 140px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.wrapper img {
    display: inline-block;
}

a + a {
  margin-left: 20px;
}

footer {
  font-size: 20px;
  color: white;
  background-color: black;
  padding: 80px 140px;
}

.ngc-footer-top {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-items: center;
            align-items: center;
}

.ngc-footer-bottom {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 48px;
    font-size: 16px;
}

.ngc-footer-bottom .locations {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    line-height: 1.6;
}

.ngc-footer-bottom .locations div, .ngc-footer-bottom div p {
    margin: 10px 80px 10px 0;
}

@media only screen and (max-width: 1024px) {
    footer {
        padding: 48px 48px;
    }
}

@media only screen and (max-width: 768px) {
    footer {
        padding: 26px 48px;
    }
}
.cta-wrapper {
  border-bottom: 1px solid #cccccc;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.cta-wrapper h1 {
  margin-left: 140px;
  -webkit-flex: 2 1;
          flex: 2 1;
}

@media only screen and (max-width: 1080px) {
  .cta-wrapper h1 {
    margin-left: 50px;
  }
}

@media only screen and (max-width: 800px) {
  .cta-wrapper h1 {
    margin-left: 50px;
  }
}

.primary-image > .right {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.primary-image > img {
  margin: 0 62px;
}

.primary-image > .right > img {
  margin: 0 72px;
}

.primary-image > .right > div {
  padding: 106px 72px;
  font-size: 30px;
  text-align: center;
  background-color: #c00000;
  color: white;
}

.wrapper-values {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  grid-template-columns: repeat(3, 1fr);
}

.wrapper-values > div {
  border-top: 1px solid #cccccc;
  padding: 80px 85px;
  transition: 0.3s;
  -webkit-flex: 1 1 20%;
          flex: 1 1 20%;
}

.wrapper-values div + div {
  border-left: 1px solid #cccccc;
}

.wrapper-values > div:first-child {
  background-color: #c00000;
  color: white;
}

.wrapper-values > div:hover {
  background-color: #c00000;
  color: white;
  border-left: 0px;
}

@media only screen and (max-width: 1080px) {
  .wrapper-values > div {
    padding: 12px 48px;
    -webkit-flex: 1 1 30%;
            flex: 1 1 30%;
  }
}

@media only screen and (max-width: 800px) {
  .wrapper-values > div {
    padding: 12px 48px;
    -webkit-flex: 1 1 50%;
            flex: 1 1 50%;
  }
  .primary-image > .right > img {
    margin: 0 16px;
  }
  .primary-image > .right > div {
    padding: 42px 42px;
    font-size: 20px;
  }
}

